<template>
  <div class="nav-container" :class="{solid: isNavSolid}">
    <div
      class="content"
      :class="{ showNavList: isShowNavList }"
    >
      <div class="nav-logo">
        <img alt="default" src="../assets/logo.png">
        <div class="logo-title theme-color-one theme-font-one">AA娱乐</div>
      </div>
      <div class="nav-menu">
        <ul class="menu">
          <li class="item theme-color-one theme-font-one">
            首页
          </li>
          <li class="item theme-color-one theme-font-one">
            品牌介绍
          </li>
          <li class="item theme-color-one theme-font-one">
            包网案例
          </li>
          <li class="item theme-color-one theme-font-one">
            产品优势
          </li>
          <li class="item theme-color-one theme-font-one">
            联系方式
          </li>
        </ul>
      </div>
      <div class="nav-contact">
        <ul class="contacts theme-color-two">
          <li>
            <div class="icon">
              <img alt="default" src="../assets/phone.png">
            </div>
            <div class="theme-font-one theme-color-five">联系我们</div>
          </li>
          <li>
            <div class="icon">
              <img  alt="default" src="../assets/globe.png">
            </div>
            <div class="theme-font-one theme-color-five">Intl-简体中文</div>
            <div class="icon-arrow-down"></div>
          </li>
        </ul>
      </div>
    </div>
    <div
      class="nav-menu-icon"
      @click="showMenuItem"
    >
      <div class="icon-bars" :class="{ open: isShowNavList }">
        <span></span>
        <span></span>
        <span></span>
        <span></span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isShowNavList: false,
      isNavSolid: false,
    };
  },
  methods: {
    showMenuItem() {
      this.isShowNavList = !this.isShowNavList;
    },
    detectScroll() {
      const currentRec = document.documentElement.getBoundingClientRect();
      if (currentRec.y < -65) {
        this.isNavSolid = true;
      } else {
        this.isNavSolid = false;
      }
    },
  },
  mounted() {
    window.addEventListener('scroll', this.detectScroll, true);
  },
};
</script>
